:root {
    --font-family-1: 'Urbanist', sans-serif;
    --font-family-2: 'be_vietnam_proregular', sans-serif;
}
/* Customer Story Section Styling */
.fno-customer-story__main-container {
	max-width: 1270px;
	padding: 0 35px;
	margin: 140px auto 140px;
}
/* Customer Story Main Wrapper Styling */
.fno-customer-story__wrapper {
	height: auto;
	position: relative;
	padding: 50px 100px 0;
	max-height: 616px;
	filter: drop-shadow(0 10px 23px rgba(111,137,163,0.2));
}
/* Image Overlay Styling */
.fno-customer-story__background-overlay {
	position: absolute;
	inset: 0;
	background-image: linear-gradient(90deg, #ab0b3d 0%, #af0a3b 8%, #bc0737 18%, #d30430 27%, #ea002a 35%, #e70363 51%, #a50695 65%, #9e28b5 100%);
	opacity: 0.596;
}

.fno-customer-story__background-overlay::after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #826456;
	opacity: 0.1;
	z-index: 2;
}
/* Heading Styling */
.fno-customer-story__heading {
	position: relative;
	font-family: var(--font-family-1);
}

.fno-customer-story__heading h4 {
	font-size: 24px;
	font-weight: 700;
	color: white;
}
/* Customer Story Slider Styling */
.fno-customer-story__slider-wrapper {
	position: relative;
	top: 50px;
}

.fno-customer-story__story-meta-info {
	display: flex;
	align-items: center;
	gap: 4.5rem;
	margin: 0 auto 40px;
	height: auto;
}
/* Individual story image styling */
.fno-customer-story__story-meta-info img {
	width: 118px;
	height: 100px;
}
/* Individual story heading styling */
.fno-customer-story__story-meta-info h4 {
	font-size: 32px;
	line-height: 42px;
	color: #ffffff;
	font-weight: 700;
	font-family: var(--font-family-1);
}
/* Individual story summary styling */
.fno-customer-story__slider-summary-info {
	padding: 50px 70px 60px 115px;
	filter: drop-shadow(0 10px 23px rgba(111,137,163,0.2));
	background-color: #ffffff;
}

.fno-customer-story__story-short-desc {
	position: relative;
	display: flex;
}

.fno-customer-story__story-short-desc p {
	font-size: 24px;
	line-height: 40px;
	color: #000000;
	font-weight: 600;
	margin: 0;
	flex: 1;
	font-family: var(--font-family-1);
}

.fno-customer-story__story-short-desc p span.quote-start{
	display: inline-block;
	position: absolute;
	top: 1px;
	left: -50px;
}

.fno-customer-story__story-short-desc p span.quote-end {
	position: relative;
	bottom: -10px;
	left: 15px;
}
/* Individual story author details stylings */
.fno-customer-story__story-extra-details {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 50px 0 40px 0;
	gap: 2rem;
	flex-wrap: wrap;
}

.fno-customer-story__story-author-details img {
	width: 50px;
	border-radius: 50%;
	height: 50px;
}

.fno-customer-story__story-author-details {
	display: flex;
	align-items: center;
	gap: 20px;
}

.fno-customer-story__author-meta-details {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	font-family: var(--font-family-2);
}

.fno-customer-story__author-meta-details span {
	font-size: 20px;
	line-height: 26px;
	color: #000000;
	font-weight: 400;
}

.fno-customer-story__author-meta-details span:last-child {
	color: lightgray;
}

.fno-customer-story__story-video-details {
	cursor: pointer;
	border-radius: 27px;
	padding: 12px 16px;
	background-color: #ffffff;
	filter: drop-shadow(0 4px 11.5px rgba(111,137,163,0.2));
}

.fno-customer-story__story-video-details a {
	display: flex;
	align-items: center;
	gap: 10px;
	font-size: 14px;
	line-height: 26px;
	color: #000000;
	font-weight: 700;
	font-family: var(--font-family-1);
	text-decoration: none;
}

.fno-customer-story__story-more-details {
	border-radius: 25px;
	padding: 16px 30px;
	border: 2px solid #0069c1;
	background-color: #ffffff;
	filter: drop-shadow(0 10px 23px rgba(111,137,163,0.2));
	font-family: var(--font-family-1);
	cursor: pointer;
}

.fno-customer-story__story-more-details  a {
	display: flex;
	align-items: center;
	gap: 10px;
	text-decoration: none;
	font-size: 18px;
	font-weight: 600;
	color: #000000;
}

.fno-customer-story__story-slider-details {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.fno-customer-story__slider-counting {
	font-size: 12px;
	line-height: 50px;
	font-weight: 500;
	font-family: var(--font-family-1);
}

.fno-customer-story__story-slider-details::after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 4px;
	background: linear-gradient(90deg, #EA002A 0%, #E50695 19%, #61CAC9 43%, #C1D500 56%, #CDD208 61%, #EDCA1D 69%, #FFC529 74%, #FF9E24 82%, #FF671C 92%, #BB4700 100%);
}
/* Slider related stylings */
.fno-customer-story__slider-wrapper .slick-dots {
	bottom: 10px;
}

.fno-customer-story__slider-wrapper .slick-dots li button::before {
	font-size: 30px;
	color: #e0dfe4;
	opacity: 1;
}

.fno-customer-story__slider-wrapper .slick-dots li.slick-active button:before {
	opacity: 1;
	color: #2670ca;
}

/* Media Queries */
@media screen and (min-width:1025px) and (max-width:1300px) {

	.fno-customer-story__wrapper {
		max-height: none;
	}

	.fno-customer-story__slider-wrapper {
		top: 60px;
	}

	.fno-customer-story__story-meta-info h4 {
		font-size: 26px;
	}

	.fno-customer-story__story-extra-details {
		justify-content: space-around;
	}
}

@media screen and (max-width:1024px) {

	.fno-customer-story__main-container {
		margin: 140px auto 50px;
	}

	.fno-customer-story__wrapper {
		padding: 50px 0px 0;
		max-height: none;
	}

	.fno-customer-story__heading h4 {
		text-align: center;
		font-size: 22px;
	}

	.fno-customer-story__slider-wrapper {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		top: 30px;
	}

	.fno-customer-story__story-meta-info {
		flex-direction: column;
		justify-content: center;
		text-align: center;
		gap: 0;
	}

	.fno-customer-story__story-meta-info img {
		width: 100px;
		height: 60px;
	}

	.fno-customer-story__story-meta-info h4 {
		width: 80%;
		margin: 22px 0 0;
		font-size: 26px;
		line-height: 36px;
		text-align: center;
	}

	.fno-customer-story__slider-summary-info {
		width: 100%;
		padding: 35px 30px;
	}

	.fno-customer-story__story-short-desc p {
		font-size: 20px;
		line-height: 32px;
		text-align: center;
	}

	.fno-customer-story__story-short-desc p span.quote-start,
	.fno-customer-story__story-short-desc p span.quote-end{
		position: initial;
	}
	
	.fno-customer-story__story-short-desc p span.quote-start svg,
	.fno-customer-story__story-short-desc p span.quote-end svg{
		width: 12px;
	}

	.fno-customer-story__story-extra-details {
		justify-content: center;
	}

	.fno-customer-story__story-video-details span {
		display: none;
	}
}

@media screen and (max-width:650px) {

	.fno-customer-story__slider-counting {
		display: none;
	}
}

@media screen and (max-width:425px) {
	
	.fno-customer-story__story-more-details {
		padding: 16px;
	}

	.fno-customer-story__story-more-details a {
		font-size: 16px;
	}
}